header .headerLogin {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: #6244A3;
  z-index: 11111;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: none;
}
header .headerLogin h3 {
  font-size: 35px;
  font-weight: 700;
  color: black;
  letter-spacing: 2px;
  margin-bottom: 30px;
}
@media (max-width: 991px) {
  header .headerLogin h3 {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  header .headerLogin h3 {
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 20px;
  }
}
header .headerLogin form {
  text-align: center;
}
header .headerLogin form input {
  width: 300px;
  margin: 20px 0px;
  padding: 10px 20px;
  border-radius: 5px;
  border: 2px solid black;
  background-color: transparent;
  color: white;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
}
header .headerLogin form input:focus {
  outline: none;
}
header .headerLogin form input::placeholder {
  color: white;
  text-transform: capitalize;
  font-size: 13px;
  letter-spacing: 2px;
}
header .headerLogin form .pass {
  position: relative;
}
header .headerLogin form .pass .fa {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: black;
  font-size: 14px;
}
header .headerLogin form button {
  padding: 10px 30px;
  color: white;
  background-color: transparent;
  outline: 2px solid black;
  border: none;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 2px;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  background-color: black;
}
header .headerLogin form button::after {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  right: 0px;
  background-color: black;
  transform: translateX(100px);
  z-index: -11;
}
header .headerLogin form button::before {
  content: "";
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: black;
  transform: translateX(-100px);
  z-index: -11;
}
header .headerLogin form button:hover::after {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header .headerLogin form button:hover::before {
  transition: 1s;
  cursor: pointer;
  transform: translateX(0px);
}
header .headerLogin .close {
  position: absolute;
  top: 10px;
  right: 30px;
  display: block;
  border: 2px solid black;
  padding: 15px 20px;
  color: white;
  border-radius: 5px;
  font-size: 18px;
  transition: 0.3s;
  cursor: pointer;
}
header .headerLogin .close:hover {
  background-color: black;
  transform: rotate(180deg);
}
